* {
  padding: 0;
  margin: 0;
}

/* navbar css */
.logo-with-menuicon {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 10px;
}
.logo-with-text {
  margin-left: 1rem;
  display: flex;
  align-items: center;
  text-decoration: none;
  text-transform: capitalize;
  color: black;
}
.feature-bar {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}
.search-bar:focus {
  outline: none;
}
.search-bar {
  width: 30rem;
  padding: 10px;
  border: 1px solid #e0e0e0;
}
/* navbar css */

/* sidebar css  */
.sidebar-logo-icon {
  padding: 20px 0px 0px;
}
.sidebar-button {
  background: white;
  border: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 8px 0;
  cursor: pointer;
}
.sidebar-button:hover {
  background: #e9e9e9;
}
.active {
  background: #e9e9e9;
}
.sidebar-icon-name {
  font-size: 1rem;
  margin-left: 1rem;
}
/* sidebar css  */

/* Video Information css */
.thumbs-up-icon:hover {
  color: red;
  cursor: pointer;
}
/* Video Information css*/

/*Search Feed Container */
.circle {
  width: 20px;
  height: 20px;
  background: #fff;
  margin: 5px 10px;
  transform-origin: top center;
  animation: spin 2s linear infinite alternate; /* 일정하게 무제한 */
  border: 10px solid red;
}
@keyframes spin {
  0% {
    transform: rotate(0) scale(1.1);
  }
  50% {
    transform: rotate(180deg) scale(0.2);
  }
  100% {
    transform: rotate(360deg) scale(1.1);
  }
}
.row {
  display: flex;
  flex: 0 1 100%;
}

/*Search Feed Container */

/*Responsive css */
@media screen and (max-width: 800px) {
  .navbar-container {
    flex-direction: column !important;
  }
  .search-bar {
    width: 16rem;
  }
  .responsive-search-card {
    display: flex !important;
    flex-direction: column !important;
    max-height: 500px !important;
    max-width: 100px !important;
    min-width: 350px !important;
  }
  .ultra-search-card {
    min-width: 90% !important;
    max-width: 90% !important;
  }
  .ultra-responsive-image {
    width: 200px !important;
  }
  .react-player {
    width: auto !important;
    height: 300px !important;
  }
  .video-container {
    flex-direction: column !important;
  }
  .row {
    display: none !important;
  }
}
@media screen and (max-width: 1000px) and (min-width: 801px) {
  .search-bar {
    width: 12rem;
  }
  .responsive-search-card {
    min-width: 30rem !important;
    max-width: 30rem !important;
  }
  .responsive-image {
    width: 250px !important;
    height: 200px !important;
  }
  .react-player {
    width: 25rem !important;
    height: 350px !important;
  }
  .ultra-search-card {
    min-width: 90% !important;
    max-width: 90% !important;
  }
  .ultra-responsive-image {
    width: 160px !important;
  }
}

@media screen and (max-width: 1300px) and (min-width: 1001px) {
  .react-player {
    width: auto !important;
    height: 450px !important;
  }
  .ultra-search-card {
    margin-right: 1rem !important;
  }
}
@media screen and (max-width: 1480px) and (min-width: 1301px) {
  .react-player {
    width: auto !important;
    height: 510px !important;
  }
}
